@import "theme";
@import "functions";

body {
	display: table;
	width: 100%;
	height: 100%;
	font-family: $font-family;
	background-color: #fff;
}

.navbar {
  border-color: #D1831D;
}

.navbar-header {
  background: #DF9028;
  height: 90px;
  width: 90px;
    border-right: 1px #D1831D solid;
}

.navbar-brand {
  line-height: 90px;
  text-align: center;
  margin-top: 22.5px;
  margin-left: 9px;
  padding:0px;
  height: auto;
}

.dropdown-toggle {
  height: 90px;
    padding-left: 25px !important;
    padding-right: 25px !important;
    line-height: 60px !important;
    font-weight: bold;
    color: white !important;
    text-transform: uppercase;
}

.dropdown-toggle:hover {
  background-color: none;
}

.navbar-default .navbar-nav>.open>a, .navbar-default .navbar-nav>.open>a:focus, .navbar-default .navbar-nav>.open>a:hover {
    color: #555;
    background-color: transparent;
    opacity: 0.8;
}

.navbar {
  background: #333;
}


.panel-default {
  border: none;
  border-color: #DF9028;
}

.panel-default .panel-heading {
    color: #333;
    background-color: #fff;
    border-color: #DF9028;
}

.panel-heading {
    padding: 10px 15px;
    padding-top: 12px;
    border-bottom: 2px solid #DF9028;
    font-weight: bold;
    font-size: 15px;
    text-transform: uppercase;
}

//Comment
input, textarea {
  outline: none;
  border: none;
  display: block;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  font-size: rem(16);
  color: grey;
}

p {
  line-height: rem(21);
}

.comments {
  margin: rem(40) auto 0;
  max-width: rem(972);
  padding: 0 rem(20);
}

.comment-wrap {
  margin-bottom: rem(20);
  display: table;
  width: 100%;
  min-height: rem(85);
}

.photo {
  //background-color: #ff6666;
  padding-top: rem(10);
  display: table-cell;
  width: rem(56);
  
  .avatar {
    //background-color: #0094ff;
    border-radius: 50%;
    background-size: contain;
  }
}

.comment-block {
  padding: rem(16);
  background-color: #fff;
  display: table-cell;
  vertical-align: top;
  border-radius: rem(3);
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.08);
  
  textarea {
    width: 100%;
    resize: none;
  }
}

.comment-text {
  margin-bottom: rem(20);
}

.bottom-comment {
  color: #acb4c2;
  font-size: rem(14);
}

.comment-date {
  float: left;
}

.comment-actions {
  float: right;
  
  li {
    display: inline;
    margin: -2px;
    cursor: pointer;
    
    &.complain {
      padding-right: rem(12);
      border-right: 1px solid #e1e5eb;
    }
    
    &.reply {
      padding-left: rem(12);
      padding-right: rem(2);
    }
    
    &:hover {
      color: blue;
      //text-decoration: underline;
    }
  }
}


//Footer
footer {
	display: table-row;
	.-footer {
		background-color: $secondary-bg;
		padding: 40px 0px 25px 0px;
		.-label {
			font-weight: bold;
			font-size: 13px;
			text-transform: uppercase;
			margin: 0px 0px 7.5px 0px;
			padding: 0px;
		}
		.-list {
			width: 100%;
			margin: 0;
			padding: 0;
			list-style-type: none;
			font-size: 13px;
			margin-bottom: 15px;
			li {
				padding: 2px 0px;
			}
		}
	}
	.-subfooter {
		background-color: #333;
		width: 100%;
		height: 35px;
		line-height: 34px;
		color: $font-clr;
		font-size: 11px;
	}
}

a {
	&.back-to-top {
		display: none;
		width: 45px;
		height: 45px;
		text-indent: -9999px;
		position: fixed;
		z-index: 999;
		right: 20px;
		bottom: 20px;
		background: #DF9028 url("http://html-tuts.com/demo/back-to-top/up-arrow.png") no-repeat center 43%;
		border-radius: 5px;
	}
}



.navbar-default .navbar-toggle .icon-bar {
      background-color: #fff;
    }

.breadcrumb {
  margin-bottom: 15px;

	.active {
		font-weight: bold;
	}
}

//UI

.ui-card {
    max-width: 100%;
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 290px;
    min-height: 0;
    background: #FFF;
    padding: 0;
    border: none;
    border-radius: .28571429rem;
    box-shadow: 0 1px 3px 0 #D4D4D5, 0 0 0 1px #D4D4D5;
    -webkit-transition: box-shadow .1s ease,-webkit-transform .1s ease;
    transition: box-shadow .1s ease,-webkit-transform .1s ease;
    transition: box-shadow .1s ease,transform .1s ease;
    transition: box-shadow .1s ease,transform .1s ease,-webkit-transform .1s ease;
    z-index: '';

	.content {
	    -webkit-box-flex: 1;
	    -webkit-flex-grow: 1;
	    -ms-flex-positive: 1;
	    flex-grow: 1;
	    border: none;
	    border-top: 1px solid rgba(34,36,38,.1);
	    background: 0 0;
	    margin: 0;
	    padding: 1em;
	    box-shadow: none;
	    font-size: 1em;
	    border-radius: 0;
	}

	.extra {
	    max-width: 100%;
	    min-height: 0!important;
	    -webkit-box-flex: 0;
	    -webkit-flex-grow: 0;
	    -ms-flex-positive: 0;
	    flex-grow: 0;
	    border-top: 1px solid rgba(0,0,0,.05)!important;
	    position: static;
	    background: 0 0;
	    width: auto;
	    margin: 0;
	    padding: .75em 1em;
	    top: 0;
	    left: 0;
	    color: rgba(0,0,0,.4);
	    box-shadow: none;
	    -webkit-transition: color .1s ease;
	    transition: color .1s ease;
	}
}

.ui-card:after {
    display: block;
    content: ' ';
    height: 0;
    clear: both;
    overflow: hidden;
    visibility: hidden;
}


